import React, { Component } from "react";
import { getCetakUlang } from "../../actions/cetakUlangAction";
import { getUUID } from "../../actions/fakturAction";
import { getsupplierList } from "../../actions/supplierAction";
import Axios from "axios";
import { connect } from "react-redux";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import notrespond from "../../Images/500.svg";
import Skeleton from "react-loading-skeleton";
import { Link } from "react-router-dom";
import { gagal } from "../../Alert";
import HeaderCard from "../../HeaderCard";
import { AxiosMasterGet } from "../../Axios";
import FormLaporanBuktiPenjualanHead from "./FormLaporanBuktiPenjualanHead";
import { getToday } from "../../getDate";
import PrintNota from "../../Stocking/Component/PrintNota";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;
const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class FormLaporanBuktiPenjualan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listSupplier: [],
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "no_bon",
          text: "Nomor Bon",
        },
        {
          dataField: "nama_toko",
          text: "Nama Toko",
        },
        {
          dataField: "ongkos_rp",
          text: "ongkos (Rp)",
        },
        {
          dataField: "dics_global",
          text: "Disc Global",
        },
        {
          dataField: "total_netto",
          text: "Netto (Gr)",
        },
        {
          dataField: "total_netto_ext",
          text: "Netto Ext (Gr)",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-warning mr-2"
                  onClick={() => this.print(row)}
                >
                  <i className="fa fa-print mr-1"></i>
                  Print
                </button>
              </div>
            );
          },
        },
      ],
      data: [
        {
          tanggal: "2020-11-12",
        },
      ],
      listPenerimaan: [],
    };
  }
  componentDidMount() {
    this.props.dispatch(getsupplierList());
    this.props.dispatch(getUUID());
    this.props.dispatch(getCetakUlang());
  }

  getData(data) {
    AxiosMasterGet(
      `report/penjualan/${data.kode_sales}&${data.tanggalAwal}&${data.tanggalAkhir}`
    ).then((res) =>
      this.setState({
        listPenerimaan: res.data,
      })
    );
  }
  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  pilihSupplier = (input) => (e) => {
    var supp = e.target.value ? e.target.value : "DEFALT | DEFAULT";
    var result_explode = supp.split("|");
    this.setState({
      kodeSupplier: result_explode[0],
      namaSupplier: result_explode[1],
    });
  };

  caridata() {
    console.log("PENCARIAN....");
    Axios.get(serverNonMaster + "cst/dc/pjl/heads", {
      params: {
        tglFrom: this.state.tglFrom,
        tglTo: this.state.tglTo,
        noBon: this.state.noBon,
        kodeSupplier: this.state.kodeSupplier,
      },
    })
      .then((res) =>
        this.setState({
          hasilDataGet: res.data,
        })
      )
      .catch(() => gagal("Data Kosong"));
  }

  print(data) {
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = data.detail_barang;
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        ++i,
        ticket.kadar_cetak,
        ticket.kode_jenis,
        parseFloat(ticket.bruto).toFixed(3),
        ticket.kadar_jual,
        parseFloat(ticket.disc).toFixed(3),
        parseFloat(ticket.netto).toFixed(3),
        "",
        1,
        "",
        "",
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    const footer = [
      "",
      "",
      "",
      table
        .map((list) => parseFloat(list.bruto))
        .reduce((a, b) => a + b, 0)
        .toFixed(3),
      "",
      table
        .map((list) => parseFloat(list.disc))
        .reduce((a, b) => a + b, 0)
        .toFixed(3),
      table
        .map((list) => parseFloat(list.netto))
        .reduce((a, b) => a + b, 0)
        .toFixed(3),
      "0",
      table.map((list) => parseFloat(1)).reduce((a, b) => a + b, 0),
      "",
      "",
    ];
    tableRows.push(footer);
    console.log(JSON.stringify(tableRows));
    var columTable = [
      "No",
      "Kadar",
      "Kode",
      "Bruto \n(Gr)",
      "Harga \n(%)",
      "Diskon",
      "Netto \n(Gr)",
      "Pkg",
      "Qty",
      "Ongkos \n(Rp)",
      "Grand Total\nNetto Ext",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    PrintNota(
      "no Trx",
      data.no_bon,
      "Tanggal",
      data.tanggal,
      "No Bon",
      data.no_bon,
      "",
      "",
      "ADMIN",
      getToday(),
      "",
      columTable,
      "Transaksi Penjualan",
      tableRows
    );
  }
  render() {
    return (
      <>
        <div className="container-fluid">
          <ol className="breadcrumb mb-2">
            <Link to="/cetakUlangBukti">
              <button type="button" className="btn btn-secondary">
                <i className="fa fa-angle-left mr-3" aria-hidden="true"></i>{" "}
                Back
              </button>
            </Link>
          </ol>
          <div className="card">
            <HeaderCard title="Cetak Ulang Bukti Penjualan" />
            <div className="card-body">
              <br />
              <div className="row">
                <div className="col-lg-12">
                  <FormLaporanBuktiPenjualanHead
                    onSubmit={(data) => this.getData(data)}
                  />
                </div>
                <div className="col-lg-12">
                  {this.state.listPenerimaan ? (
                    <ToolkitProvider
                      bootstrap4
                      keyField={this.state.listPenerimaan}
                      data={this.state.listPenerimaan}
                      columns={this.state.columns}
                      search
                    >
                      {(props) => (
                        <div className="col-lg-12 col-md-12 mt-5">
                          <div className="text-right">
                            <SearchBar
                              {...props.searchProps}
                              className="form-control"
                              placeholder="Tuliskan Kode / Nama"
                              style={{ margin: 0 }}
                            />
                          </div>
                          <BootstrapTable
                            {...props.baseProps}
                            headerClasses="header-table"
                            striped
                            hover
                            bordered={true}
                            pagination={paginationFactory()}
                          />
                          <ExportCSVButton
                            {...props.csvProps}
                            className="btn btn-primary"
                          >
                            Export CSV
                          </ExportCSVButton>
                        </div>
                      )}
                    </ToolkitProvider>
                  ) : (
                    <div>
                      {this.props.error ? (
                        <div className="text-center">
                          <img
                            src={notrespond}
                            style={{ width: "30%" }}
                            alt="NOT RESPOND"
                          />
                          <br />
                          <h4>Request API Gagal, Silahkan Refresh</h4>
                        </div>
                      ) : (
                        <Skeleton width="100%" height={180} />
                      )}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default connect()(FormLaporanBuktiPenjualan);
