import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { getsalesmanList } from "../../actions/salesmanAction";
import { renderField, renderFieldSelect } from "../../renderField";

class FormLaporanBuktiPenjualanHead extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount() {
    this.props.dispatch(getsalesmanList());
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-lg-3">
            <Field
              name="tanggalAwal"
              component={renderField}
              type="date"
              label="Tanggal Awal "
              placeholder="Masukan Tanggal Awal "
            />
          </div>
          <div className="col-lg-3">
            <Field
              name="tanggalAkhir"
              component={renderField}
              type="date"
              label="Tanggal Akhir "
              placeholder="Masukan Tanggal Akhir "
            />
          </div>
          <div className="col-lg-3">
            <Field
              name="kode_sales"
              component={renderFieldSelect}
              options={this.props.salesman.map((list) => {
                let data = {
                  value: list.kode_sales,
                  name: list.nama_sales,
                };
                return data;
              })}
              type="text"
              label="Sales"
              placeholder="Masukan Sales"
            />
          </div>
          <div className="col-md-12 col-lg-12 mt-3">
            <div className="row">
              <div className="col-lg-12 col-md-12">
                <div className="text-right">
                  <button className="btn btn-primary">
                    <i className="fa fa-search mr-1"></i>
                    Cari Data
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormLaporanBuktiPenjualanHead = reduxForm({
  form: "FormLaporanBuktiPenjualanHead",
  enableReinitialize: true,
})(FormLaporanBuktiPenjualanHead);
export default connect((state) => {
  return {
    salesman: state.provinsi.salesman,
    dataLokasiPindah: state.provinsi.dataLokasiPindah,
  };
})(FormLaporanBuktiPenjualanHead);
